body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.animated {
  -webkit-animation-duration: .5s;
          animation-duration: .5s; }

.animated-1s {
  -webkit-animation-duration: 1s;
          animation-duration: 1s; }

.animated-2s {
  -webkit-animation-duration: 2s;
          animation-duration: 2s; }

.customFadeIn {
  -webkit-animation-name: customFadeIn;
          animation-name: customFadeIn; }

@-webkit-keyframes customFadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes customFadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.customZoomIn {
  -webkit-animation-name: customZoomIn;
          animation-name: customZoomIn;
  -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
          animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

@-webkit-keyframes customZoomIn {
  0% {
    transform: scale(0.5);
    opacity: 0.8; }
  100% {
    transform: scale(1);
    opacity: 1; } }

@keyframes customZoomIn {
  0% {
    transform: scale(0.5);
    opacity: 0.8; }
  100% {
    transform: scale(1);
    opacity: 1; } }

.customClipX {
  -webkit-animation-name: customClipX;
          animation-name: customClipX;
  transform-origin: 50% 50%;
  -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
          animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

@-webkit-keyframes customClipX {
  0% {
    transform: scaleY(0.1);
    opacity: 0.8; }
  100% {
    transform: scaleX(1);
    opacity: 1; } }

@keyframes customClipX {
  0% {
    transform: scaleY(0.1);
    opacity: 0.8; }
  100% {
    transform: scaleX(1);
    opacity: 1; } }

.customUpAndDown {
  -webkit-animation-name: customUpAndDown;
          animation-name: customUpAndDown;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: 10;
          animation-iteration-count: 10; }

@-webkit-keyframes customUpAndDown {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(20px); }
  100% {
    transform: translateY(0); } }

@keyframes customUpAndDown {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(20px); }
  100% {
    transform: translateY(0); } }

.customBreath {
  -webkit-animation-name: customBreath;
          animation-name: customBreath;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

@-webkit-keyframes customBreath {
  0% {
    transform: scale(1);
    color: #fff; }
  50% {
    transform: scale(1);
    color: #e65015;
    text-shadow: 0 0 3px #fff; }
  100% {
    transform: scale(1);
    color: #fff; } }

@keyframes customBreath {
  0% {
    transform: scale(1);
    color: #fff; }
  50% {
    transform: scale(1);
    color: #e65015;
    text-shadow: 0 0 3px #fff; }
  100% {
    transform: scale(1);
    color: #fff; } }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

.my-container {
  width: 1200px;
  margin: 0 auto; }

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

.empty-info {
  text-align: center;
  font-size: 16px;
  color: #999;
  padding: 100px 0; }
  .empty-info p.content .anticon {
    font-size: 48px;
    position: relative;
    top: 8px;
    margin-right: 10px; }

.modal-form .ant-form-item {
  margin-bottom: 0px; }

.ant-card-actions > li > span {
  display: block; }

.ant-card-actions .card-action-item {
  display: block; }

.ant-table .ant-btn {
  margin: 0 4px; }

.ant-table-small > .ant-table-content > .ant-table-body {
  margin: 0 !important; }

.fs16 {
  font-size: 16px; }

.text-333 {
  color: #666; }

.login-link:hover {
  text-decoration: underline; }

.login-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  color: #f2f2f2;
  font-size: 12px;
  padding: 15px; }
  .login-footer p {
    margin-bottom: 4px; }

.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column; }

